<script>
/* eslint-disable */
import { mapGetters } from 'vuex'
import firebase from 'firebase'
import swal from 'sweetalert2'
import formatCurrency from 'format-currency'
import Ckeditor from 'vue-ckeditor2'

export default {
    components: {
        Ckeditor
    },
    data() {
        return {
            config: {
                height: 150
            },
            loading: false,
            systemLoadingDialog: false,
            user: "",
            //
            pageEventsBgImgSrc: '',
            singleEventBgImgSrc: '',
            singleEventContentImgSrc: '',
            pageEventsBgImg: '',
            singleEventBgImg: '/static/img/question.jpg',
            singleEventContentImg: '/static/img/question.jpg',
            //
            updateContent: [],
            //
            eventAdd: false,
            eventEdit: false,
            eventManage: true,
            eventFormBtn: '新增活動',
            eventContent: {
                title: '',
                // brief: '',
                date: '',
                time: '',
                location: '',
                fee: '',
                // subTitle: '',
                paragraph1: '',
                // paragraph2: ''
            },
            menu: false,
            date: null,
            //
            pagination: {
                sortBy: 'date',
                descending: 'desc'
            },
            search: '',
            selectedEvents: [],
            headersEvents: [
               {
                    text: '圖片',
                    value: 'singleEventContentImg'
                },
                // {
                //     text: '新增時間',
                //     align: 'left',
                //     sortable: true,
                //     value: 'addDateTime'
                // },
                { text: '活動名稱', sortable: true, value: 'name' },
                { text: '活動日期', sortable: true, value: 'date' },
                { text: '活動時間', sortable: true, value: 'time' },
                // { text: '活動地點', value: 'location' },
                // { text: '活動費用', value: 'fee' },
                { text: '', value: 'edit' },
                { text: '', value: 'delete' }
            ],
            itemsEvents: []
        }
    },
    created() {
        this.getEventsMain()
        this.getEvents()
    },
    methods: {
        //
        async getEventsMain () {
            try {
                var db = firebase.firestore()
                var doc = await db.collection('eventsMain').doc('eventsMain').get()
                this.updateContent = doc.data()
                this.pageEventsBgImg = doc.data().pageEventsBgImg
            } catch (error) {
                console.error(error)
            }
        },
        async getEvents () {
            try {
                var db = firebase.firestore()
                var now = this.parseDateTimeFormat('date')
                var query = await db.collection('events').orderBy('date', 'desc').orderBy('time').get()
                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
                this.itemsEvents = data
                // console.log(data)
            } catch (error) {
                console.error(error)
            }
        },
        //
        async updateEventsMain () {
            try {
                this.systemLoadingDialog = true
                var db = firebase.firestore()
                if (this.pageEventsBgImgSrc) {
                    this.uploadImg('eventsMain', 'pageEventsBgImg', this.pageEventsBgImgSrc)
                }
                await db.collection('eventsMain').doc('eventsMain').update({
                    eventsMainTitle: this.updateContent.eventsMainTitle
                })
                swal(
                    '更新成功',
                    '',
                    'success'
                )
                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        async eventSubmit () {
            try {
                var db = firebase.firestore()
                if (this.eventAdd) {
                    // if (this.singleEventBgImgSrc === '' || this.singleEventContentImgSrc === '') {
                    if (this.singleEventContentImgSrc === '') {
                        swal(
                            '圖片未正確上傳',
                            '',
                            'warning'
                        )
                        return
                    }
                    this.systemLoadingDialog = true
                    this.eventContent.addDateTime = this.parseDateTimeFormat()
                    var docRef = await db.collection('events').add(this.eventContent)
                    // this.uploadImg(docRef.id, 'singleEventBgImg', this.singleEventBgImgSrc)
                    this.uploadImg(docRef.id, 'singleEventContentImg', this.singleEventContentImgSrc)
                    // console.log(this.eventContent)
                    this.systemLoadingDialog = false
                    this.eventAdd = false
                    this.eventEdit = false
                    this.eventManage = true
                    this.getEvents()
                    swal(
                        '新增成功',
                        '',
                        'success'
                    )
                } else if (this.eventEdit) {
                    this.systemLoadingDialog = true
                    // if (this.singleEventBgImgSrc) {
                    //     this.uploadImg(this.eventContent.docId, 'singleEventBgImg', this.singleEventBgImgSrc)
                    // }
                    if (this.singleEventContentImgSrc) {
                        this.uploadImg(this.eventContent.docId, 'singleEventContentImg', this.singleEventContentImgSrc)
                    }
                    await db.collection('events').doc(this.eventContent.docId).update(this.eventContent)
                    this.eventAdd = false
                    this.eventEdit = false
                    this.eventManage = true
                    this.getEvents()
                    this.systemLoadingDialog = false
                    swal(
                        '更新成功',
                        '',
                        'success'
                    )
                }
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        //
        async deleteEvent (docId) {
            try {
                var result = await swal({
                    title: '你確定嗎？',
                    text: "此動作將無法收回",
                    type: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: '確認刪除',
                    cancelButtonText: '取消'
                })
                if (result.value) {
                    this.systemLoadingDialog = true
                    var db = firebase.firestore()
                    await db.collection('events').doc(docId).delete()
                    this.systemLoadingDialog = false
                    this.getEvents()
                    swal(
                        '新增成功',
                        '',
                        'success'
                    )
                }
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        //
        addEventBtnClicked () {
            this.eventContent = {
                title: '',
                brief: '',
                date: '',
                time: '',
                location: '',
                fee: '',
                subTitle: '',
                paragraph1: '',
                paragraph2: ''
            }
            this.singleEventBgImgSrc = ''
            this.singleEventBgImg = '/static/img/question.jpg'
            this.singleEventContentImgSrc = ''
            this.singleEventContentImg = '/static/img/question.jpg'
            this.eventAdd = true
            this.eventManage = false
            this.eventEdit = false
            this.eventFormBtn = '新增活動'
        },
        editEventBtnClicked (item) {
            // console.log(item)
            this.eventContent = item
            this.singleEventBgImgSrc = ''
            this.singleEventContentImgSrc = ''
            this.singleEventBgImg = item.singleEventBgImg
            this.singleEventContentImg = item.singleEventContentImg
            this.eventAdd = false
            this.eventManage = false
            this.eventEdit = true
            this.eventFormBtn = '更新活動'
        },
        manageEventBtnClicked () {
            this.eventAdd = false
            this.eventEdit = false
            this.eventManage = true
        },
        //
        async uploadImg (docId, updateRef, targetFile) {
            console.log('updating')

            var db = firebase.firestore()
            var storage = firebase.storage();
            var storageRef = firebase.storage().ref();
            
            var file = targetFile

            // Create the file metadata
            var metadata = {
                contentType: file.type
            };

            // Upload file and metadata to the object 'images/mountains.jpg'
            var uploadTask = storageRef.child('images/CMSAbout/' + file.name).put(file, metadata);

            // Listen for state changes, errors, and completion of the upload.
            uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED, // or 'state_changed'
            function(snapshot) {
                // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
                var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                console.log('Upload is ' + progress + '% done');
                switch (snapshot.state) {
                case firebase.storage.TaskState.PAUSED: // or 'paused'
                    console.log('Upload is paused');
                    break;
                case firebase.storage.TaskState.RUNNING: // or 'running'
                    console.log('Upload is running');
                    break;
                }
            }, function(error) {

            // A full list of error codes is available at
            // https://firebase.google.com/docs/storage/web/handle-errors
            switch (error.code) {
                case 'storage/unauthorized':
                // User doesn't have permission to access the object
                break;
                case 'storage/canceled':
                // User canceled the upload
                break;
                case 'storage/unknown':
                // Unknown error occurred, inspect error.serverResponse
                break;
            }
            }, async () => {
                try {
                    // Upload completed successfully, now we can get the download URL
                    var downloadURL = await uploadTask.snapshot.ref.getDownloadURL()
                    // console.log('File available at', downloadURL)
                    var data = {}
                    data[updateRef] = downloadURL
                    if (docId === 'eventsMain') {
                        await db.collection('eventsMain').doc('eventsMain').update(data)
                    } else {
                        await db.collection('events').doc(docId).update(data)
                    }
                    
                } catch (error) {
                    console.error(error)
                }
            });
        },
        onFilePicked (event) {
            // see which button has been activated
            const btn = event.target.name
            // console.log(btn)
            const files = event.target.files
            let filename = files[0].name
            // if the file doesnt have extension
            if (filename.lastIndexOf('.') <= 0) {
                alert('Please add a valid file!')
            }
            // turn this binary file to string value, for later uploads
            const fileReader = new FileReader()
            fileReader.addEventListener('load', () => {
                // for image preview
                switch (btn) {
                case 'fileInput_1':
                    this.pageEventsBgImg = fileReader.result
                    break;
                case 'fileInput_2':
                    this.singleEventBgImg = fileReader.result
                    break;
                case 'fileInput_3':
                    this.singleEventContentImg = fileReader.result
                    break;
                default: 
                    break;  
                }
            })
            switch (btn) {
                case 'fileInput_1':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.pageEventsBgImgSrc = files[0]
                    break;
                case 'fileInput_2':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.singleEventBgImgSrc = files[0]
                    break;
                case 'fileInput_3':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.singleEventContentImgSrc = files[0]
                    break;
                default: 
                    break;  
                }
        },
        onFilePickedBtn (number) {
            switch (number) {
                case 1:
                    this.$refs.fileInput_1.click()
                    break;
                case 2:
                    this.$refs.fileInput_2.click()
                    break;
                case 3:
                    this.$refs.fileInput_3.click()
                    break;
                default: 
                break;  
            }
        },
        parseDateTimeFormat (type) {
            var now = new Date()
            var mm = now.getMonth() + 1 // getMonth() is zero-based
            var dd = now.getDate()
            var hour = now.getHours()
            var min = now.getMinutes()
            var sec = now.getSeconds()
            if (type === 'date') {
                var string = now.getFullYear() + '-' + (mm > 9 ? '' : '0') + mm + '-' + (dd > 9 ? '' : '0') + dd
                return string
            } else {
                var string = now.getFullYear() + '/' + (mm > 9 ? '' : '0') + mm + '/' + (dd > 9 ? '' : '0') + dd + ' ' + (hour > 9 ? '' : '0') + hour + ':' + (min > 9 ? '' : '0') + min + ':' + (sec > 9 ? '' : '0') + sec
                return string
            }
        },
        currency (money) {
            let opts = { format: '%s%v', symbol: '$' }
            return formatCurrency(money, opts)
        }
    },    
    watch: {
        //
    },
    computed: {
        ...mapGetters({
            getLang: 'getLang'
        })
    }
};
</script>


