                                            min-width="290px"
                                            :return-value.sync="date">
                                            <v-text-field
                                                slot="activator"
                                                label="活動日期"
                                                v-model="eventContent.date"
                                                prepend-icon="event"
                                                readonly
                                            ></v-text-field>
                                            <v-date-picker v-model="eventContent.date" no-title scrollable locale="zh-cn">
                                                <v-spacer></v-spacer>
                                                <v-btn flat color="primary" @click="menu = false">取消</v-btn>
                                                <v-btn flat color="primary" @click="$refs.menu.save(date)">OK</v-btn>
                                            </v-date-picker>
                                        </v-menu>
                                    </v-flex>
                                    <v-flex xs12 sm6>
                                        <v-text-field
                                            label="活動時間"
                                            v-model="eventContent.time"
                                            required></v-text-field>        
                                    </v-flex>
                                </v-layout>
                                <v-text-field
                                    label="活動地點"
                                    v-model="eventContent.location"
                                    required></v-text-field>
                                <v-text-field
                                    label="活動費用"
                                    v-model="eventContent.fee"
                                    required></v-text-field>
                                <v-text-field
                                    label="副標題"
                                    v-model="eventContent.subTitle"
                                    required></v-text-field>
                                <textarea  
                                    rows="5"
                                    v-model="eventContent.paragraph1"
                                    style="border: 1px solid black; width: 100%;"
                                    placeholder="內文 段落一"
                                ></textarea>
                                <textarea  
                                    rows="5"
                                    v-model="eventContent.paragraph2"
                                    style="border: 1px solid black; width: 100%;"
                                    placeholder="內文 段落二"
                                ></textarea>
                            </v-flex>
                            <v-flex xs12 sm6>
                                <v-layout row wrap>
                                    <v-flex xs12 sm4>
                                        <h4 class="blue white--text text-xs-center pt-1 pb-1">背景圖片</h4>
                                        <img :src="singleEventBgImg" alt="image" class="img-responsive" />
                                    </v-flex>
                                    <v-flex xs12 sm4>
                                        <v-btn
                                            class="gradient-warning"
                                            :loading="loading"
                                            @click.native="loader = 'loading'"
                                            :disabled="loading"
                                            @click="onFilePickedBtn(2)">
                                            上傳圖片
                                            <v-icon right dark>cloud_upload</v-icon>
                                        </v-btn>
                                        <input 
                                            type="file" 
                                            accept="image/*" 
                                            @change="onFilePicked" 
                                            style="display: none;" 
                                            ref="fileInput_2"
                                            name="fileInput_2">        
                                    </v-flex>
                                </v-layout>
                                <v-layout row wrap>
                                    <v-flex xs12 sm4>
                                        <h4 class="blue white--text text-xs-center pt-1 pb-1">內文圖片</h4>
                                        <img :src="singleEventContentImg" alt="image" class="img-responsive" />
                                    </v-flex>
                                    <v-flex xs12 sm4>
                                        <v-btn
                                            class="gradient-warning"
                                            :loading="loading"
                                            @click.native="loader = 'loading'"
                                            :disabled="loading"
                                            @click="onFilePickedBtn(3)">
                                            上傳圖片
                                            <v-icon right dark>cloud_upload</v-icon>
                                        </v-btn>
                                        <input 
                                            type="file" 
                                            accept="image/*" 
                                            @change="onFilePicked" 
                                            style="display: none;" 
                                            ref="fileInput_3"
                                            name="fileInput_3">        
                                    </v-flex>
                                </v-layout>
                            </v-flex>
                            <v-flex xs12 sm12>
                                <v-btn class="success" @click="eventSubmit">
                                    {{ eventFormBtn }}
                                </v-btn>
                            </v-flex>
                        </v-layout>
                    </v-flex>
                </v-layout>
            </v-card-text>
        </v-card>

        <v-card class="mb-30" v-if="eventManage">
            <v-card-title primary-title class="indigo white--text">
                <h3>管理活動</h3>
            </v-card-title>
            <v-card-text>    
                <v-layout row wrap>
                    <v-flex xs12 sm12>
                        <v-data-table
                            :headers="headersEvents"
                            :items="itemsEvents"
                            :search="search"
                            v-model="selectedEvents"
                            item-key="name">
                            <template slot="items" slot-scope="props">
                                <td><img :src="props.item.singleEventContentImg" width="50px"></td>
                                <td class="text-xs-left">{{ props.item.title }}</td>
                                <td class="text-xs-left">{{ props.item.date }}</td>
                                <td class="text-xs-left">{{ props.item.time }}</td>
                                <td>
                                    <v-btn class="primary" block @click="editEventBtnClicked(props.item)">
                                        編輯
                                    </v-btn>
                                </td>
                                <td>
                                    <v-btn class="btn-danger" block @click="deleteEvent(props.item.docId)">
                                        刪除
                                    </v-btn>
                                </td>
                            </template>
                        </v-data-table>
                    </v-flex>
                </v-layout>
            </v-card-text>
        </v-card>
    </v-flex>
</v-layout>
-->


