                    break;
                case 'fileInput_7':
                    this.teacherImg3 = fileReader.result
                    break;
                case 'fileInput_8':
                    this.teacherImg4 = fileReader.result
                    break;
                case 'fileInput_9':
                    this.moreEventsImg = fileReader.result
                    break;
                case 'fileInput_10':
                    this.teacherBgImg = fileReader.result
                    break;
                case 'fileInput_11':
                    this.actImg1 = fileReader.result
                    break;
                case 'fileInput_12':
                    this.actImg2 = fileReader.result
                    break;
                case 'fileInput_13':
                    this.actImg3 = fileReader.result
                    break;
                case 'fileInput_14':
                    this.actImg4 = fileReader.result
                    break;
                case 'fileInput_15':
                    this.actImg5= fileReader.result
                    break;                                                       
                default: 
                    break;  
                }
            })
            switch (btn) {
                case 'fileInput_1':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.pageHeaderImgSrc1 = files[0]
                    break;
                case 'fileInput_2':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.pageHeaderImgSrc2 = files[0]
                    break;
                case 'fileInput_3':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.pageHeaderImgSrc3 = files[0]
                    break;
                case 'fileInput_4':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.pageHeaderImgSrc4 = files[0]
                    break;
                case 'fileInput_5':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.teacherImgSrc1 = files[0]
                    break;
                case 'fileInput_6':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.teacherImgSrc2 = files[0]
                    break;
                case 'fileInput_7':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.teacherImgSrc3 = files[0]
                    break;
                case 'fileInput_8':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.teacherImgSrc4 = files[0]
                    break;
                case 'fileInput_9':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.moreEventsImgSrc = files[0]
                    break;
                case 'fileInput_10':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.teacherBgImgSrc = files[0]
                    break;
                case 'fileInput_11':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.actImgSrc1 = files[0]
                    break;
                case 'fileInput_12':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.actImgSrc2 = files[0]
                    break;
                case 'fileInput_13':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.actImgSrc3 = files[0]
                    break;
                case 'fileInput_14':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.actImgSrc4 = files[0]
                    break;
                case 'fileInput_15':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.actImgSrc5 = files[0]
                    break;
                default: 
                    break;  
                }
            },
        onFilePickedBtn (number) {
            switch (number) {
                case 1:
                    this.$refs.fileInput_1.click()
                    break;
                case 2:
                    this.$refs.fileInput_2.click()
                    break;
                case 3:
                    this.$refs.fileInput_3.click()
                    break;
                case 4:
                    this.$refs.fileInput_4.click()
                    break;
                case 5:
                    this.$refs.fileInput_5.click()
                    break;
                case 6:
                    this.$refs.fileInput_6.click()
                    break;
                case 7:
