                    break;
                case 8:
                    this.$refs.fileInput_8.click()
                    break;
                case 9:
                    this.$refs.fileInput_9.click()
                    break;
                case 10:
                    this.$refs.fileInput_10.click()
                    break;
                case 11:
                    this.$refs.fileInput_11.click()
                    break;
                case 12:
                    this.$refs.fileInput_12.click()
                    break;
                case 13:
                    this.$refs.fileInput_13.click()
                    break;
                case 14:
                    this.$refs.fileInput_14.click()
                    break;
                case 15:
                    this.$refs.fileInput_15.click()
                    break;
                default: 
                break;  
            }
        },
    },    
    watch: {
        selectedHotLessons: function () {
            if (this.selectedHotLessons.length > 6) {
                swal(
                    '注意',
                    '最多只能選擇六個課程',
                    'warning'
                )
                for (var i = this.selectedHotLessons.length; i > 6; i--) {
                    this.selectedHotLessons.pop()
                }
            }
        }
    },
    computed: {
        ...mapGetters({
            getLang: 'getLang'
        })
    }
};
</script>



