<template>
    <v-container fluid grid-list-xl py-0>
        <v-layout row wrap align-center>
            <v-flex xs12 class="text-xs-center">
                <v-dialog v-model="systemLoadingDialog" max-width="500px" class="mx-auto">
                    <v-card class="text-xs-center">
                        <v-card-text>
                        <h3>{{ $lang.strings.system_running}}......</h3>
                        <br>
                        <v-progress-circular indeterminate :size="70" :width="7" color="blue"></v-progress-circular>
                        </v-card-text>
                    </v-card>
                </v-dialog>
            </v-flex>
        </v-layout>
        <h3>{{$lang.strings.users_info}}</h3>
        <v-layout row wrap>
            <v-flex xs12 sm12>
                <v-card class="mb-30">
                    <v-card-title class="blue white--text">
                        用戶列表
                    </v-card-title>
                    <v-card-text>
                        <v-layout row wrap>
                            <v-flex xs12 sm12>
                                <v-data-table
                                    v-bind:headers="headersUsers"
                                    :items="itemsUsers"
                                    hide-actions>
                                <template slot="items" slot-scope="props">
                                    <td class="text-xs-left">{{ props.item.name }}</td>
                                    <td class="text-xs-left">{{ props.item.realName }}</td>
                                    <td class="text-xs-left">{{ props.item.email }}</td>
                                    <td class="text-xs-left">{{ props.item.phoneNumber }}</td>
                                    <td class="text-xs-left">{{ props.item.gender }}</td>
                                    <td class="text-xs-left">{{ props.item.licenseNumber }}</td>
                                    <td>
                                        <v-btn class="info white--text" block @click="getUserLessons(props.item.uid, props.item.name)">
                                            查看課程
                                        </v-btn>
                                    </td>
                                </template>
                                </v-data-table>
                            </v-flex>
                        </v-layout>
                    </v-card-text>
                </v-card>
            </v-flex>
        </v-layout>
        <!-- lessons table -->
        <v-layout row wrap align-center>
            <v-flex xs12 class="text-xs-center">
                <v-dialog v-model="lessonsDialog" max-width="600px" class="mx-auto">
                    <v-card class="text-xs-center">
                        <v-card-text>
                            <h3>{{ lessonsDialogTitle }} 購買之課程</h3>
                            <v-layout row wrap>
                                <v-flex xs12 sm12>
                                    <v-data-table
                                        v-bind:headers="headersLessons"
                                        :items="itemsLessons"
                                        hide-actions>
                                    <template slot="items" slot-scope="props">
                                        <td class="text-xs-left">{{ props.item.lessonName }}</td>
                                        <td class="text-xs-left">{{ props.item.lessonCategory }}</td>
                                        <td class="text-xs-left">{{ currency(props.item.price) }}</td>
                                    </template>
                                    </v-data-table>
                                </v-flex>
                            </v-layout>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="pink lighten-2" class="white--text" outline @click="lessonsDialog = false">關閉</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
/* eslint-disable */
import { mapGetters } from 'vuex'
import firebase from 'firebase'
import swal from 'sweetalert2'
import formatCurrency from 'format-currency'

export default {
    data() {
        return {
            systemLoadingDialog: false,
            // users table
            headersUsers: [
                { text: '顯示名稱', value: 'name' },
                { text: '真實名稱', value: 'realName' },
                { text: 'Email', value: 'email' },
                { text: '電話', value: 'phoneNumber' },
                { text: '性別', value: 'gender' },
                { text: '證件號碼', value: 'licenseNumber' },
                { text: '查看課程', value: 'getLessons' },
            ],
            itemsUsers: [],
            // lessons table
            lessonsDialog: false,
            lessonsDialogTitle: '',
            headersLessons: [
                { text: ' 課程名稱', value: 'lessonName' },
                { text: '課程類別', value: 'lessonCategory' },
                { text: '價格', value: 'price' },
            ],
            itemsLessons: []
        }
    },
    created() {
        this.getUsers()
    },
    methods: {
        async getUsers () {
            this.systemLoadingDialog = true
            const db = firebase.firestore()
            try {
                let query = await db.collection('users').where('informationFilled', '==', true).get()
                let users = []
                query.forEach(doc => {
                    users.push(doc.data())
                })
                this.itemsUsers = users
                this.systemLoadingDialog = false
            } catch (e) {
                this.systemLoadingDialog = false
                console.log(e)
            }
        },
        async getUserLessons (docId, name) {
            this.systemLoadingDialog = true
            const db = firebase.firestore()
            try {
                let query = await db.collection('myLessons')
                    .where('userUid', '==', docId)
                    .where('adminVerified', '==', true)
                    .get()
                let lessons = []
                query.forEach(doc => {
                    lessons.push(doc.data())
                })
                this.itemsLessons = lessons
                this.systemLoadingDialog = false
                // constructing lessons dialog
                this.lessonsDialogTitle = name
                this.lessonsDialog = true
            } catch (e) {
                console.log(e)
            }
        },
        currency (money) {
            let opts = { format: '%s%v', symbol: '$' }
            return formatCurrency(money, opts)
        }
    },    
    watch: {
        //
    },
    computed: {
        ...mapGetters({
            getLang: 'getLang'
        })
    }
};
</script>


