    methods: {
        async getActivities () {
            try {
                console.log("getActivities")
                var db = firebase.firestore()
                var query = await db.collection('activities').orderBy('date').get()
                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
                this.itemsActivities = data

                console.log(this.itemsActivities)
            } catch (error) {
                console.error(error)
            }
        },

        async activitySubmit () {
            try {
                console.log("activitySubmit")
                var db = firebase.firestore()
                if (this.activityAdd) {
                    this.systemLoadingDialog = true
                    // this.activityContent.addDateTime = this.parseDateTimeFormat()
                    var docRef = await db.collection('activities').add(this.activityContent)
                    if (this.actImgSrc1) {
                        this.uploadImg(docRef.id, 'actImg1', this.actImgSrc1)
                    }
                    if (this.actImgSrc2) {
                         this.uploadImg(docRef.id, 'actImg2', this.actImgSrc2)
                    }
                    if (this.actImgSrc3) {
                        this.uploadImg(docRef.id, 'actImg3', this.actImgSrc3)
                    }
                    if (this.actImgSrc4) {
                        this.uploadImg(docRef.id, 'actImg4', this.actImgSrc4)
                    }
                    if (this.actImgSrc5) {
                        this.uploadImg(docRef.id, 'actImg5', this.actImgSrc5)
                    }
                    if (this.actImgSrc6) {
                        this.uploadImg(docRef.id, 'actImg6', this.actImgSrc6)
                    }

                    this.systemLoadingDialog = false
                    this.activityAdd = false
                    this.activityEdit = false
                    this.activityManage = true
                    swal(
                        '新增成功',
                        '',
                        'success'
                    ).then(async(result) => {
                        await this.getActivities();
                    })
                   

                } else if (this.activityEdit) {
                    this.systemLoadingDialog = true
                    console.log("this.actImgSrc1")
                    console.log(this.actImgSrc1);
                    if (this.actImgSrc1) {
                        this.uploadImg(this.activityContent.docId, 'actImg1', this.actImgSrc1)
                    }
                    if (this.actImgSrc2) {
                        this.uploadImg(this.activityContent.docId, 'actImg2', this.actImgSrc2)
                    }
                    if (this.actImgSrc3) {
                        this.uploadImg(this.activityContent.docId, 'actImg3', this.actImgSrc3)
                    }
                    if (this.actImgSrc4) {
                        this.uploadImg(this.activityContent.docId, 'actImg4', this.actImgSrc4)
                    }
                    if (this.actImgSrc5) {
                        this.uploadImg(this.activityContent.docId, 'actImg5', this.actImgSrc5)
                    }
                    if (this.actImgSrc6) {
                        this.uploadImg(this.activityContent.docId, 'actImg6', this.actImgSrc6)
                    }
                    await db.collection('activities').doc(this.activityContent.docId).update(this.activityContent)

                    this.activityAdd = false
                    this.activityEdit = false
                    this.activityManage = true
                    this.systemLoadingDialog = false
                    swal(
                        '更新成功',
                        '',
                        'success'
                    ).then(async(result) => {
                        await this.getActivities();
                    })
                }
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        //
        async deleteActivity (docId) {
            console.log(docId)
            try {
                var result = await swal({
                    title: '你確定嗎？',
                    text: "此動作將無法收回",
                    type: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: '確認刪除',
                    cancelButtonText: '取消'
                })
                if (result.value) {
                    this.systemLoadingDialog = true
                    var db = firebase.firestore()
                    let result = await db.collection('activities').doc(docId).delete()
                    console.log("Wait to delete");
                    console.log(result);
                    this.getActivities()
                    this.systemLoadingDialog = false
                    location.reload()
                    
                    swal(
