            this.actImg4 = item.actImg4
            this.actImg5 = item.actImg5
            this.actImg6 = item.actImg6

            this.activityAdd = false
            this.activityManage = false
            this.activityEdit = true
            this.activityFormBtn = '更新活動花絮'
        },
        manageActivityBtnClicked () {
            this.activityAdd = false
            this.activityEdit = false
            this.activityManage = true
        },
        ////////////////////////////////Event Video List///////////////////////////////
        async getEvl() {
            try {
                var db = firebase.firestore()
                var query = await db.collection('events-videos-list').get()
                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
                this.itemsEvl = data
            } catch (error) {
                console.error(error)
            }
        },
        async evlSubmit () {
            try {
                var db = firebase.firestore()
                if (this.evlAdd) {
                    this.systemLoadingDialog = true
                    this.evlContent.updateTime = this.parseDateTimeFormat()
                    var docRef = await db.collection('events-videos-list').add(this.evlContent)
                    
                    this.systemLoadingDialog = false
                    this.evlAdd = false
                    this.evlEdit = false
                    this.evlManage = true
                    this.getEvl()
                    this.getEvideoListsToSet()
                    swal(
                        '新增成功',
                        '',
                        'success'
                    )
                } else if (this.evlEdit) {
                    this.systemLoadingDialog = true
                    this.evlContent.updateTime = this.parseDateTimeFormat()
                    await db.collection('events-videos-list').doc(this.evlContent.docId).update(this.evlContent)
                    this.evlAdd = false
                    this.evlEdit = false
                    this.evlManage = true
                    this.getEvl()
                    this.getEvideoListsToSet()
                    this.systemLoadingDialog = false
                    swal(
                        '更新成功',
                        '',
                        'success'
                    )
                }
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        //
        async deleteEvl (docId) {
            try {
                var result = await swal({
                    title: '你確定嗎？',
                    text: "此動作將無法收回，此播放清單中有影片的話會遺失",
                    type: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: '確認刪除',
                    cancelButtonText: '取消'
                })
                if (result.value) {
                    this.systemLoadingDialog = true
                    var db = firebase.firestore()
                    await db.collection('events-videos-list').doc(docId).delete()
                    this.systemLoadingDialog = false
                    this.getEvl()
                    this.getEvideoListsToSet()
                    swal(
                        '刪除成功',
                        '',
                        'success'
                    )
                }
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        //
        addEvlBtnClicked () {
            this.evlContent = {
                name: ''
            }
            this.evlAdd = true
            this.evlManage = false
            this.evlEdit = false
            this.evlFormBtn = '新增播放清單'
        },
        editEvlBtnClicked (item) {
            // console.log(item)
            this.evlContent = item
            this.evlAdd = false
            this.evlManage = false
            this.evlEdit = true
            this.evlFormBtn = '更新播放清單'
        },
        manageEvlBtnClicked () {
            this.evlAdd = false
            this.evlEdit = false
            this.evlManage = true
        },
        ////////////////////////////////Event Video///////////////////////////////
        async getEvideoListsToSet () {
            try {
                this.systemLoadingDialog = true
                var db = firebase.firestore()
                var query = await db.collection('events-videos-list').get()
                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
                this.evideoLists = data
                this.evideoList = this.evideoLists[0].name
                //
                this.selectEvideoLists = data
                this.selectEvideoList = this.selectEvideoLists[0].name
                // manage select cateogry is binded on watch field
                // this.getLessonsOfCat(this.categories[0].name)
                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        async getEvideosFrom (evlName) {
            try {
                this.systemLoadingDialog = true
                var db = firebase.firestore()
                // console.log(cat)
                var query = await db.collection('events-videos')
                    .where('evListName', '==', evlName)
                    .get()
                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
                this.itemsEvideos = data
                // console.log(this.selectEvideoList)
                // console.log(this.itemsEvideos)

                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        async evideosFormSubmit () {
            this.systemLoadingDialog = true
            try {
                var db = firebase.firestore()
                if (this.addEvideoStatus) {
                    // console.log(this.evideoList);
                    this.evideoContent.evListName = this.evideoList;
                    this.evideoContent.updateTime = this.parseDateTimeFormat();
                    var docRef = await db.collection('events-videos').add(this.evideoContent)

                    swal(
                        '影片新增成功',
                        '',
                        'success'
                    )
                    this.systemLoadingDialog = false
                    this.getEvideosFrom(this.evideoList)
                    this.addEvideoStatus = false
                    this.editEvideoStatus = false
                    this.manageEvideoStatus = true

                } else if (this.editEvideoStatus) {
                    // console.log(this.evideoContent)
                    var docRef = await db.collection('events-videos').doc(this.evideoContent.docId).update({
                        evListName: this.evideoList,
                        updateTime: this.parseDateTimeFormat(),
                        ycontent: this.evideoContent.ycontent,
                        youtubeUrl: this.evideoContent.youtubeUrl,
                        ytitle: this.evideoContent.ytitle
                    })
                    // wait until doc created
                    swal(
                        '影片更新成功',
                        '',
                        'success'
                    )
                    this.systemLoadingDialog = false
                    this.getEvideosFrom(this.evideoList)
                    this.addEvideoStatus = false
                    this.editEvideoStatus = false
                    this.manageEvideoStatus = true
                }
            } catch (error) {
                console.error(error)
                this.systemLoadingDialog = false
            }
        },
        async deleteEvideo (evlName, docId) {
            try {
                var result = await swal({
                    title: '確認刪除？',
                    text: "此動作將無法收回",
                    type: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: '確認',
                    cancelButtonText: '取消',
                })
                if (!result.value) {
                    return
                }
                var db = firebase.firestore()
                await db.collection('events-videos').doc(docId).delete()
                swal(
                    '刪除成功',
                    '',
                    'success'
                )
                this.getEvideosFrom(evlName)
            } catch (error) {
                console.error(error)
            }
        },
        addEvideoBtnClicked () {
            this.addEvideoStatus = true
            this.editEvideoStatus = false
            this.manageEvideoStatus = false
            this.evideoMode = '新增影片'
            this.evideoModeBtn = '新增'
            this.evideoContent = {}
        },
        editEvideoBtnClicked (docId) {

            this.addEvideoStatus = false
            this.editEvideoStatus = true
            this.manageEvideoStatus = false
            this.evideoMode = '編輯影片'
            this.evideoModeBtn = '更新'

            for (var i = 0; i < this.itemsEvideos.length; i++) {
                if (this.itemsEvideos[i].docId === docId) {
                    this.evideoList = this.itemsEvideos[i].evListName
                    this.evideoContent = this.itemsEvideos[i]
                    return
                }
            }
        },
        manageEvideoBtnClick () {
            this.getEvideosFrom(this.evideoList)
            this.manageEvideoStatus = true
            this.addEvideoStatus = false
            this.editEvideoStatus = false
            this.evideoMode = '管理影片'
        },
        //
        async uploadImg (docId, updateRef, targetFile) {
            // console.log('updating')
            var db = firebase.firestore()
            var storage = firebase.storage();
            var storageRef = firebase.storage().ref();
            
            var file = targetFile

            // Create the file metadata
            var metadata = {
                contentType: file.type
            };

            // Upload file and metadata to the object 'images/mountains.jpg'
            var uploadTask = storageRef.child('images/CMSActivity/' + file.name).put(file, metadata);

            // Listen for state changes, errors, and completion of the upload.
            uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED, // or 'state_changed'
            function(snapshot) {
                // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
                var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                console.log('Upload is ' + progress + '% done');
                switch (snapshot.state) {
                case firebase.storage.TaskState.PAUSED: // or 'paused'
                    console.log('Upload is paused');
                    break;
                case firebase.storage.TaskState.RUNNING: // or 'running'
                    console.log('Upload is running');
                    break;
                }
            }, function(error) {

            // A full list of error codes is available at
            // https://firebase.google.com/docs/storage/web/handle-errors
            switch (error.code) {
                case 'storage/unauthorized':
                // User doesn't have permission to access the object
                break;
                case 'storage/canceled':
                // User canceled the upload
                break;
                case 'storage/unknown':
                // Unknown error occurred, inspect error.serverResponse
                break;
            }
            }, async () => {
                try {
                    // Upload completed successfully, now we can get the download URL
                    var downloadURL = await uploadTask.snapshot.ref.getDownloadURL()
                    // console.log('File available at', downloadURL)
                    var data = {}
                    data[updateRef] = downloadURL
                    await db.collection('activities').doc(docId).update(data)
                    
                } catch (error) {
                    console.error(error)
                }
            });
        },
        onFilePicked (event) {
            // see which button has been activated
            const btn = event.target.name
            console.log(btn)
            const files = event.target.files
            let filename = files[0].name
            // if the file doesnt have extension
            if (filename.lastIndexOf('.') <= 0) {
                alert('Please add a valid file!')
            }
            console.log(filename)
            // turn this binary file to string value, for later uploads
            const fileReader = new FileReader()
            fileReader.addEventListener('load', () => {
                // for image preview
                switch (btn) {
                    case 'fileInput_11':
                        this.actImg1 = fileReader.result
                        break;
                    case 'fileInput_12':
                        this.actImg2 = fileReader.result
                        break;
                    case 'fileInput_13':
                        this.actImg3 = fileReader.result
                        break;
                    case 'fileInput_14':
                        this.actImg4 =  fileReader.result
                        break;
                    case 'fileInput_15':
                        this.actImg5 = fileReader.result
                        break;
                    case 'fileInput_16':
                        this.actImg6 = fileReader.result
                        break;
                    default: 
                        break;  
                }
            })
            switch (btn) {
                case 'fileInput_11':
                    fileReader.readAsDataURL(files[0])
                    this.actImgSrc1 = files[0]
                    break;
                case 'fileInput_12':
                    fileReader.readAsDataURL(files[0])
                    this.actImgSrc2 = files[0]
                    break;
                case 'fileInput_13':
                    fileReader.readAsDataURL(files[0])
                    this.actImgSrc3 = files[0]
                    break;
                case 'fileInput_14':
                    fileReader.readAsDataURL(files[0])
                    this.actImgSrc4 = files[0]
                    break;
                case 'fileInput_15':
                    fileReader.readAsDataURL(files[0])
                    this.actImgSrc5 = files[0]
                    break;
                case 'fileInput_16':
                    fileReader.readAsDataURL(files[0])
                    this.actImgSrc6 = files[0]
                    break;
                default: 
                    break;  
                }
        },
        onFilePickedBtn (number) {
            switch (number) {
                case 11:
                    this.$refs.fileInput_11.click()
                    break;
                case 12:
                    this.$refs.fileInput_12.click()
                    break;
                case 13:
                    this.$refs.fileInput_13.click()
                    break;
                case 14:
                    this.$refs.fileInput_14.click()
                    break;
                case 15:
                    this.$refs.fileInput_15.click()
                    break;
                case 16:
                    this.$refs.fileInput_16.click()
                    break;
                default: 
                    break;  
            }
        },
        parseDateTimeFormat (type) {
            var now = new Date()
            var mm = now.getMonth() + 1 // getMonth() is zero-based
            var dd = now.getDate()
            var hour = now.getHours()
            var min = now.getMinutes()
            var sec = now.getSeconds()
            if (type === 'date') {
                var string = now.getFullYear() + '-' + (mm > 9 ? '' : '0') + mm + '-' + (dd > 9 ? '' : '0') + dd
                return string
            } else {
                var string = now.getFullYear() + '/' + (mm > 9 ? '' : '0') + mm + '/' + (dd > 9 ? '' : '0') + dd + ' ' + (hour > 9 ? '' : '0') + hour + ':' + (min > 9 ? '' : '0') + min + ':' + (sec > 9 ? '' : '0') + sec
                return string
            }
        },
        currency (money) {
            let opts = { format: '%s%v', symbol: '$' }
            return formatCurrency(money, opts)
        }
    },    
    watch: {
        selectedActivities: function () {
            // location.reload()
        },
        selectEvideoList: function () {
            this.getEvideosFrom(this.selectEvideoList)
        },
    },
    computed: {
        ...mapGetters({
            getLang: 'getLang'
        })
    }
};
</script>



