<script>
/* eslint-disable */
import { mapGetters } from 'vuex'
import firebase from 'firebase'
import swal from 'sweetalert2'
import formatCurrency from 'format-currency'
import Ckeditor from 'vue-ckeditor2'
import moment from 'moment'

export default {
    components: {
        Ckeditor
    },
    data() {
        return {
            config: {
                height: 150
            },
            systemLoadingDialog: false,
            user: "",
            loading: false,
            // control panel
            addLessonStatus: false,
            editLessonStatus: false,
            manageLessonStatus: true,
            mode: '管理課程',
            modeBtn: '新增',
            //
            categories: [],
            category: '',
            itemsCategories: [],
            //
            ages: [],
            age: '',
            //
            lessonBgSrc: '',
            lessonBg: '/static/img/lessons/lessonsBg.jpg',
            lessonImgSrc: '',
            lessonImg: '/static/img/lessons/lessonsImg.jpg',
            //
            lessonCurriculumSrc: '',
            lessonCurriculumName: '',
            lessonCurriculum: '',
            //
            lessonVideoSrc: '',
            lessonVideoName: '',
            lessonVideo: '',
            //
            lessonVideo1Src: '',
            lessonVideo1Name: '',
            lessonVideo1: '',
            //
            lessonVideo2Src: '',
            lessonVideo2Name: '',
            lessonVideo2: '',
            //
            lessonFileSrc: '',
            lessonFileName: '',
            lessonFile: '',
            //
            lessonFile1Src: '',
            lessonFile1Name: '',
            lessonFile1: '',
            //
            lessonFile2Src: '',
            lessonFile2Name: '',
            lessonFile2: '',
            //
            formContent: {
                name: ''
            },
            // manage lessons
            selectCategories: [],
            selectCategory: '',
            selectAges: [],
            selectAge: '',
            headersLessons: [
                {
                    text: '課程名稱',
                    align: 'left',
                    sortable: true,
                    value: 'name'
                },
                { text: '課程價格', value: 'price' },
                { text: '類別', value: 'category' },
                { text: '年齡', value: 'age' },
                { text: '課程簡介', value: 'summary' },
                { text: '', value: 'edit' },
                { text: '', value: 'delete' }
            ],
            itemsLessons: [],
            // online lessons table
            headersOnlineLessons: [
                {
                    text: '檔案名稱',
                    align: 'left',
                    sortable: true,
                    value: 'name'
                },
                { text: '上傳時間', value: 'updateTime' },
                { text: '', value: 'delete' }
            ],
            itemsOnlineLessons: []
        }
    },
    created() {
        this.getCategories()
        this.getAges()
    },
    methods: {
        async getCategories () {
            try {
                this.systemLoadingDialog = true
                var db = firebase.firestore()
                var query = await db.collection('categories').get()
                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
                this.categories = data
                this.category = this.categories[0].name
                //
                data = data.filter(function(item) { 
                    return item.name !== "熱門課程"
                })
                this.selectCategories = data
                console.log(this.selectCategories);
                this.selectCategory = this.selectCategories[0].name
                // manage select cateogry is binded on watch field
                // this.getLessonsOfCat(this.categories[0].name)
               
                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        async getAges () {
            try {
                this.systemLoadingDialog = true
                var db = firebase.firestore()
                var query = await db.collection('age').orderBy('rank').get()
                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
               
                this.ages = data
                this.age = this.ages[0].range
                //
                this.selectAges = data
                // this.selectAge = this.selectAges[0].range
                // manage select age is binded on watch field

                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        async getLessonsOfCat (cat) {
            try {
                this.systemLoadingDialog = true
                var db = firebase.firestore()
                // console.log(cat)
                var query = await db.collection('lessons')
                    .where('category', '==', cat)
                    .orderBy('createDateTime', 'desc')
                    .get()
                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
                this.itemsLessons = data
                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        async getLessonsOfAge (age) {
            try {
                
                this.systemLoadingDialog = true
                var db = firebase.firestore()
                // console.log(cat)
                var query = await db.collection('lessons')
                    .where('age', '==', age)
                    .orderBy('createDateTime', 'desc')
                    .get()

                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
                // console.log(data)
                this.itemsLessons = data
                // console.log(data)
                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        async formSubmit () {
            this.systemLoadingDialog = true
            try {
                var db = firebase.firestore()
                if (this.addLessonStatus) {
                    if (!this.lessonBgSrc || !this.lessonImgSrc) {
                        swal(
                            '注意',
                            '課程背景及圖片未上傳',
                            'warning'
                        )
                        this.systemLoadingDialog = false
                        return
                    } else if (!this.formContent.name) {
                        swal(
                            '注意',
                            '課程名稱不可空白',
                            'warning'
                        )
                        this.systemLoadingDialog = false
                        return
                    }
                    this.formContent.category = this.category
                    this.formContent.age = this.age
                    this.formContent.createDateTime = this.parseDateTimeFormat()
                    console.log(this.formContent)
                    var docRef = await db.collection('lessons').add(this.formContent)
                    // wait until doc created
                    this.uploadImg(docRef.id, 'lessonBg', this.lessonBgSrc)
                    this.uploadImg(docRef.id, 'lessonImg', this.lessonImgSrc)
                    this.uploadFile(docRef.id, 'lessonCurriculum', this.lessonCurriculumSrc)
                    this.uploadFile(docRef.id, 'lessonVideo', this.lessonVideoSrc)
                    // this.uploadFile(docRef.id, 'lessonVideo1', this.lessonVideo1Src)
                    // this.uploadFile(docRef.id, 'lessonVideo2', this.lessonVideo2Src)
                    this.uploadFile(docRef.id, 'lessonFile', this.lessonFileSrc)
                    // this.uploadFile(docRef.id, 'lessonFile1', this.lessonFile1Src)
                    // this.uploadFile(docRef.id, 'lessonFile2', this.lessonFile2Src)

                    swal(
                        '課程新增成功',
                        '',
                        'success'
                    )
                    this.systemLoadingDialog = false
                } else if (this.editLessonStatus) {
                    if (this.lessonBgSrc) {
                        this.uploadImg(this.formContent.docId, 'lessonBg', this.lessonBgSrc)
                    }
                    if (this.lessonImgSrc) {
                        this.uploadImg(this.formContent.docId, 'lessonImg', this.lessonImgSrc)
                    }
                    if (this.lessonCurriculumSrc) {
                        this.uploadFile(this.formContent.docId, 'lessonCurriculum', this.lessonCurriculumSrc)
                    }
                    if (this.lessonVideoSrc) {
                        this.uploadFile(this.formContent.docId, 'lessonVideo', this.lessonVideoSrc)
                    }
                    // if (this.lessonVideo1Src) {
                    //     this.uploadFile(this.formContent.docId, 'lessonVideo1', this.lessonVideo1Src)
                    // }
                    // if (this.lessonVideo2Src) {
                    //     this.uploadFile(this.formContent.docId, 'lessonVideo2', this.lessonVideo2Src)
                    // }
                    if (this.lessonFileSrc) {
                        this.uploadFile(this.formContent.docId, 'lessonFile', this.lessonFileSrc)
                    }
                    // if (this.lessonFile1Src) {
                    //     this.uploadFile(this.formContent.docId, 'lessonFile1', this.lessonFile1Src)
                    // }
                    // if (this.lessonFile2Src) {
                    //     this.uploadFile(this.formContent.docId, 'lessonFile2', this.lessonFile2Src)
                    // }
                    let data = {
                        category: this.category,
                        age: (this.age || 12),
                        name: (this.formContent.name || ''),
                        brief: (this.formContent.brief || ''),
                        price: (this.formContent.price || 0),
                        title1: (this.formContent.title1 || ''),
                        paragraph1: (this.formContent.paragraph1 || ''),
                        paragraph2: (this.formContent.paragraph2 || ''),
                        title2: (this.formContent.title2 || ''),
                        paragraph3: (this.formContent.paragraph3 || ''),
                        studyUrl: (this.formContent.studyUrl || ''),
                    }
                    console.log(data)
                    let docRef = await db.collection('lessons').doc(this.formContent.docId).update(data)
                    // wait until doc created
                    swal(
                        '課程更新成功',
                        '',
                        'success'
                    )
                    this.systemLoadingDialog = false
                    this.getLessonsOfCat(this.category)
                    this.addLessonStatus = false
                    this.editLessonStatus = false
                    this.manageLessonStatus = true
                }
            } catch (error) {
                console.error(error)
                this.systemLoadingDialog = false
            }
        },
        async deleteLesson (docCat, docId) {
            try {
                var result = await swal({
                    title: '確認刪除？',
                    text: "此動作將無法收回",
                    type: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: '確認',
                    cancelButtonText: '取消',
                })
                if (!result.value) {
                    return
                }
                var db = firebase.firestore()
                await db.collection('lessons').doc(docId).delete()
                swal(
                    '刪除成功',
                    '',
                    'success'
                )
                this.getLessonsOfCat(docCat)
            } catch (error) {
                console.error(error)
            }
        },
        async deleteOnlineLesson (docId) {
            try {
                var db = firebase.firestore()
                await db.collection('onlineLessons').doc(docId).delete()
                swal(
                    '刪除成功',
                    '',
                    'success'
                )
                this.editLessonBtnClicked()
            } catch (e) {
                console.log(e)
            }
        },
        addLessonBtnClicked () {
            this.addLessonStatus = true
            this.editLessonStatus = false
            this.manageLessonStatus = false
            this.mode = '新增課程'
            this.modeBtn = '新增'
            this.formContent = {}
            this.lessonBg = '/static/img/lessons/lessonsBg.jpg'
            this.lessonImg = '/static/img/lessons/lessonsImg.jpg'
            this.lessonBgSrc = ''
            this.lessonImgSrc = ''
        },
        async editLessonBtnClicked (docId) {
            let db = firebase.firestore()
            this.addLessonStatus = false
            this.editLessonStatus = true
            this.manageLessonStatus = false
            this.mode = '編輯課程'
            this.modeBtn = '更新'
            // getting online lessons of this lesson
            let query = await db.collection('onlineLessons').where('lessonId', '==', docId).get()
            
            let onlineLessons = []
            query.forEach(doc => {
                let item = doc.data()
                item.docId = doc.id
                onlineLessons.push(item)
            })
            
            this.itemsOnlineLessons = onlineLessons
            this.itemsOnlineLessons.forEach(doc => {
                doc.updateTime = moment(doc.lessonFileUpdateTime).format('YYYY-MM-DD HH:mm:ss')
            })
            
            //
            for (var i = 0; i < this.itemsLessons.length; i++) {
                if (this.itemsLessons[i].docId === docId) {
                    this.formContent = this.itemsLessons[i]
                    this.category = this.itemsLessons[i].category
                    this.lessonBg = this.itemsLessons[i].lessonBg
                    this.lessonImg = this.itemsLessons[i].lessonImg
                    this.lessonCurriculumName = this.itemsLessons[i].lessonCurriculumName
                    this.lessonVideoName = this.itemsLessons[i].lessonVideoName
                    this.lessonVideoName = this.itemsLessons[i].lessonVideo1Name
                    this.lessonVideoName = this.itemsLessons[i].lessonVideo2Name
                    this.lessonFileName = this.itemsLessons[i].lessonFileName
                    this.lessonFile1Name = this.itemsLessons[i].lessonFile1Name
                    this.lessonFile2Name = this.itemsLessons[i].lessonFile2Name
                    return
                }
            }
        },
        manageLessonBtnClick () {
            this.getLessonsOfCat(this.category)
            this.manageLessonStatus = true
            this.addLessonStatus = false
            this.editLessonStatus = false
            this.mode = '管理課程'
        },
        async uploadImg (targetDoc, updateRef, targetFile) {
            console.log('updating')

            var db = firebase.firestore()
            var storage = firebase.storage();
            var storageRef = firebase.storage().ref();
            
            var file = targetFile

            // Create the file metadata
            var metadata = {
                contentType: file.type
            };

            // Upload file and metadata to the object 'images/mountains.jpg'
            var uploadTask = storageRef.child('images/CMS/' + file.name).put(file, metadata);

            // Listen for state changes, errors, and completion of the upload.
            uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED, // or 'state_changed'
            function(snapshot) {
                // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
                var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                console.log('Upload is ' + progress + '% done');
                switch (snapshot.state) {
                case firebase.storage.TaskState.PAUSED: // or 'paused'
                    console.log('Upload is paused');
                    break;
                case firebase.storage.TaskState.RUNNING: // or 'running'
                    console.log('Upload is running');
                    break;
                }
            }, function(error) {

                // A full list of error codes is available at
                // https://firebase.google.com/docs/storage/web/handle-errors
                switch (error.code) {
                    case 'storage/unauthorized':
                    // User doesn't have permission to access the object
                    break;
                    case 'storage/canceled':
                    // User canceled the upload
                    break;
                    case 'storage/unknown':
                    // Unknown error occurred, inspect error.serverResponse
                    break;
                }
            }, async () => {
                try {
                    // Upload completed successfully, now we can get the download URL
                    var downloadURL = await uploadTask.snapshot.ref.getDownloadURL()
                    // console.log('File available at', downloadURL)
                    var data = {}
                    data[updateRef] = downloadURL

                    await db.collection('lessons').doc(targetDoc).update(data)
                } catch (error) {
                    console.error(error)
                }
            });
        },
        //it should be changed to multiple or call it with multiple
         async uploadFile (targetDoc, updateRef, targetFile) {
            console.log('updateFileing')

            var db = firebase.firestore()
            var storage = firebase.storage();
            var storageRef = firebase.storage().ref();
            
            var file = targetFile

            // Create the file metadata
            var metadata = {
                contentType: file.type
            };

            // Upload file and metadata to the object 'images/mountains.jpg'
            var uploadTask = storageRef.child('files/CMS/' + file.name).put(file, metadata);

            // Listen for state changes, errors, and completion of the upload.
            uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED, // or 'state_changed'
            function(snapshot) {
                // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
                var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                console.log('Upload is ' + progress + '% done');
                switch (snapshot.state) {
                case firebase.storage.TaskState.PAUSED: // or 'paused'
                    console.log('Upload is paused');
                    break;
                case firebase.storage.TaskState.RUNNING: // or 'running'
                    console.log('Upload is running');
                    break;
                }
            }, function(error) {

                // A full list of error codes is available at
                // https://firebase.google.com/docs/storage/web/handle-errors
                switch (error.code) {
                    case 'storage/unauthorized':
                    // User doesn't have permission to access the object
                    break;
                    case 'storage/canceled':
                    // User canceled the upload
                    break;
                    case 'storage/unknown':
                    // Unknown error occurred, inspect error.serverResponse
                    break;
                }
            }, async () => {
                try {
                    // Upload completed successfully, now we can get the download URL
                    var downloadURL = await uploadTask.snapshot.ref.getDownloadURL()
                    // console.log('File available at', downloadURL)
                    console.log(uploadTask.snapshot.ref.name)
                    let data = {}
                    // 20181026 seperate online lessons to another collection with lesson id
                    if (updateRef == 'lessonVideo' || updateRef == 'lessonFile') {
                        //
                        let now = this.parseDateTimeFormat('docName')
                        data['lessonId'] = targetDoc
                        data[updateRef] = downloadURL
                        data[updateRef+"Name"] = uploadTask.snapshot.ref.name
                        data[updateRef+"UpdateTime"] = now
                        // create doc with doc name composed of parent ref and unix timeString
                        await db.collection('onlineLessons').doc(targetDoc + now).set(data)

                    } else {
                        data[updateRef] = downloadURL
                        data[updateRef+"Name"] = uploadTask.snapshot.ref.name
                        data[updateRef+"UpdateTime"] = this.parseDateTimeFormat('date')

                        await db.collection('lessons').doc(targetDoc).update(data)
                    }
                } catch (error) {
                    console.error(error)
                }
            });
        },
        onFilePicked (event) {
            console.log(event)
            // see which button has been activated
            const btn = event.target.name
            // console.log(btn)
            const files = event.target.files
            let filename = files[0].name
            // if the file doesnt have extension
            if (filename.lastIndexOf('.') <= 0) {
                alert('Please add a valid file!')
            }
            // turn this binary file to string value, for later uploads
            const fileReader = new FileReader()
            fileReader.addEventListener('load', () => {
                // for image preview
                switch (btn) {
                case 'fileInput_1':
                    this.lessonBg = fileReader.result
                    break;
                case 'fileInput_2':
                    this.lessonImg = fileReader.result
                    break;
                case 'fileInput_3':
                    this.lessonVideo = fileReader.result
                    this.lessonVideoName = filename
                    break;
                case 'fileInput_31':
                    this.lessonVideo1 = fileReader.result
                    this.lessonVideo1Name = filename
                    break;
                case 'fileInput_32':
                    this.lessonVideo2 = fileReader.result
                    this.lessonVideo2Name = filename
                    break;
                case 'fileInput_4':
                    this.lessonFile = fileReader.result
                    this.lessonFileName = filename
                    break;
                case 'fileInput_41':
                    this.lessonFile1 = fileReader.result
                    this.lessonFile1Name = filename
                    break;
                case 'fileInput_42':
                    this.lessonFile2 = fileReader.result
                    this.lessonFile2Name = filename
                    break;
                case 'fileInput_5':
                    this.lessonCurriculum= fileReader.result
                    this.lessonCurriculumName = filename
                    break;
                default: 
                    break;  
                }
            })
            switch (btn) {
                case 'fileInput_1':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.lessonBgSrc = files[0]
                    break;
                case 'fileInput_2':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.lessonImgSrc = files[0]
                    break;
                case 'fileInput_3':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.lessonVideoSrc = files[0]
                    break;
                case 'fileInput_31':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.lessonVideo1Src = files[0]
                    break;
                case 'fileInput_32':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.lessonVideo2Src = files[0]
                    break;
                case 'fileInput_4':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.lessonFileSrc = files[0]
                    break;
                case 'fileInput_41':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.lessonFile1Src = files[0]
                    break;
                case 'fileInput_42':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.lessonFile2Src = files[0]
                    break;
                case 'fileInput_5':
                    // store the raw file
                    fileReader.readAsDataURL(files[0])
                    this.lessonCurriculumSrc = files[0]
                    break;
                default: 
                    break;  
                }
        },
        onFilePickedBtn (number) {
            switch (number) {
                case 1:
                    this.$refs.fileInput_1.click()
                    break;
                case 2:
                    this.$refs.fileInput_2.click()
                    break;
                case 3:
                    this.$refs.fileInput_3.click()
                    break;
                case 31:
                    this.$refs.fileInput_31.click()
                    break;
                case 32:
                    this.$refs.fileInput_32.click()
                    break;
                case 4:
                    this.$refs.fileInput_4.click()
                    break;
                case 41:
                    this.$refs.fileInput_41.click()
                    break;
                case 42:
                    this.$refs.fileInput_42.click()
                    break;
                case 5:
                    this.$refs.fileInput_5.click()
                    break;
                default: 
                break;  
            }
        },
        currency (money) {
            let opts = { format: '%s%v', symbol: '$' }
            return formatCurrency(money, opts)
        },
        parseDateTimeFormat (type) {
            var now = new Date()
            var mm = now.getMonth() + 1 // getMonth() is zero-based
            var dd = now.getDate()
            var hour = now.getHours()
            var min = now.getMinutes()
            var sec = now.getSeconds()
            if (type === 'date') {
                var string = now.getFullYear() + '-' + (mm > 9 ? '' : '0') + mm + '-' + (dd > 9 ? '' : '0') + dd
                return string
            } else if (type === 'docName') {
                return Date.now()
            } else {
                var string = now.getFullYear() + '/' + (mm > 9 ? '' : '0') + mm + '/' + (dd > 9 ? '' : '0') + dd + ' ' + (hour > 9 ? '' : '0') + hour + ':' + (min > 9 ? '' : '0') + min + ':' + (sec > 9 ? '' : '0') + sec
                return string
            }
        }
    },    
    watch: {
        selectCategory: function () {
            this.getLessonsOfCat(this.selectCategory)
        },
        selectAge: function () {
            this.getLessonsOfAge(this.selectAge)
        }
    },
    computed: {
        ...mapGetters({
            getLang: 'getLang'
        })
    }
};
</script>



