<template>
    <v-container fluid grid-list-xl py-0>
        <v-layout row wrap align-center>
            <v-flex xs12 class="text-xs-center">
                <v-dialog v-model="systemLoadingDialog" max-width="500px" class="mx-auto">
                    <v-card class="text-xs-center">
                        <v-card-text>
                        <h3>{{ $lang.strings.system_running}}......</h3>
                        <br>
                        <v-progress-circular indeterminate :size="70" :width="7" color="blue"></v-progress-circular>
                        </v-card-text>
                    </v-card>
                </v-dialog>
            </v-flex>
        </v-layout>
        <h3> {{ $lang.strings.manage_categories }} </h3>
        <v-layout row wrap>
            <v-flex xs12 sm12>
                <!-- header card -->
                <v-card class="mb-30">
                    <v-card-title primary-title class="indigo white--text">
                        <h3>管理類別</h3>
                    </v-card-title>
                    <v-card-text>    
                        <v-layout row wrap>
                            <v-flex xs12 sm3 offset-sm3>
                                <v-text-field
                                    label="新增類別"
                                    v-model="category"
                                    required></v-text-field>
                            </v-flex>
                            <v-flex xs12 sm6>
                                <v-btn class="success" @click="addCategory">
                                    新增
                                </v-btn>
                            </v-flex>
                            <v-flex xs12 sm6 offset-sm3>
                                <div class="app-card-content">
                                    <v-data-table
                                        v-bind:headers="headersCategories"
                                        :items="itemsCategories"
                                        hide-actions>
                                    <template slot="items" slot-scope="props">
                                        <td>{{ props.item.name }}</td>
                                        <td>
                                            <v-btn class="btn-danger" @click="deleteCategory(props.item.docId)">
                                                刪除
                                            </v-btn>
                                        </td>
                                    </template>
                                    </v-data-table>
                                </div>
                            </v-flex>
                        </v-layout>
                    </v-card-text>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
/* eslint-disable */
import { mapGetters } from 'vuex'
import firebase from 'firebase'
import swal from 'sweetalert2'

export default {
    data() {
        return {
            systemLoadingDialog: false,
            user: "",
            category: '',
            headersCategories: [
                {
                    text: '名稱',
                    align: 'left',
                    sortable: true,
                    value: 'name'
                },
                // { text: '', value: 'edit' },
                { text: '刪除', value: 'delete' }
            ],
            itemsCategories: []
        }
    },
    created() {
        this.getCategories()
    },
    methods: {
        async getCategories () {
            try {
                this.systemLoadingDialog = true
                var db = firebase.firestore()
                var query = await db.collection('categories').get()
                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
                this.itemsCategories = data
                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        async addCategory () {
            if (!this.category) {
                console.log('nothing')
                return
            }
            this.systemLoadingDialog = true
            try {
                var db = firebase.firestore()
                await db.collection('categories').add({
                    name: this.category
                })
                swal(
                    '成功',
                    '',
                    'success'
                )
                this.getCategories()
                this.category = ''
                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
            
        },
        async deleteCategory (docId) {
            this.systemLoadingDialog = true
            try {
                var db = firebase.firestore()
                await db.collection('categories').doc(docId).delete()
                swal(
                    '刪除成功',
                    '',
                    'success'
                )
                this.getCategories()
                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        }
    },    
    watch: {
        //
    },
    computed: {
        ...mapGetters({
            getLang: 'getLang'
        })
    }
};
</script>



