<template>
    <v-container fluid grid-list-xl py-0>
        <v-layout row wrap align-center>
            <v-flex xs12 class="text-xs-center">
                <v-dialog v-model="systemLoadingDialog" max-width="500px" class="mx-auto">
                    <v-card class="text-xs-center">
                        <v-card-text>
                        <h3>{{ $lang.strings.system_running}}......</h3>
                        <br>
                        <v-progress-circular indeterminate :size="70" :width="7" color="blue"></v-progress-circular>
                        </v-card-text>
                    </v-card>
                </v-dialog>
            </v-flex>
        </v-layout>
        <h3>我的課程與活動</h3>
        <v-layout row wrap>
            <!-- lessons -->
            <v-flex xs12 sm12>
                <v-card class="mb-30">
                    <v-card-title primary-title class="indigo white--text">
                        <h3>課程</h3>
                    </v-card-title>
                    <v-card-text>
                        <v-layout row wrap>
                            <v-flex xs12 sm12>
                                <v-card class="mb-30">
                                    <v-card-title class="blue white--text">
                                        加入的課程(向右滑動了解詳情)
                                    </v-card-title>
                                    <v-card-text>
                                        <v-layout row wrap>
                                            <v-flex xs12 sm12>
                                                <v-data-table
                                                    :headers="headersAddedLessons"
                                                    :items="itemsAddedLessons"
                                                    :search="search"
                                                    item-key="docId">
                                                    <template slot="headerCell" slot-scope="props">
                                                        <v-tooltip bottom>
                                                            <span slot="activator">
                                                                {{ props.header.text }}
                                                            </span>
                                                            <span>
                                                                {{ props.header.text }}
                                                            </span>
                                                        </v-tooltip>
                                                    </template>
                                                    <template slot="items" slot-scope="props">
                                                        <td>{{ props.item.applyDateTime }}</td>
                                                        <td class="text-xs-left">{{ props.item.lessonName }}</td>
                                                        <td class="text-xs-left">{{ currency(props.item.price) }}</td>
                                                        <td class="text-xs-left">{{ props.item.lessonCategory }}</td>
                                                        <td class="text-xs-left">{{ props.item.verificationCode }}</td>
                                                        <td>
                                                            <v-btn class="info" block @click="showLessonTransferInfo(props.item)">
                                                                匯款資訊
                                                            </v-btn>
                                                        </td>
                                                        <td>
                                                            <v-btn class="primary" block @click="confirmedAppliedLesson(props.item.docId)" v-if="!props.item.userStatus">
                                                                已確認匯款
                                                            </v-btn>
                                                            <h4 class="indigo--text text-xs-center" v-if="props.item.userStatus === 'Confirmed'">等待驗證中</h4>
                                                        </td>
                                                        <td>
                                                            <v-btn class="btn-danger" block @click="deleteAppliedLesson(props.item.docId)" v-if="!props.item.userStatus">
                                                                刪除
                                                            </v-btn>
                                                        </td>
                                                    </template>
                                                </v-data-table>
                                            </v-flex>
                                        </v-layout>
                                    </v-card-text>
                                </v-card>
                            </v-flex>
                            <v-flex xs12 sm12>
                                <v-card class="mb-30">
                                    <v-card-title class="blue white--text">
                                        已購買課程(向右滑動了解詳情)
                                    </v-card-title>
                                    <v-card-text>
                                        <v-layout row wrap>
                                            <v-flex xs12 sm12>
                                                <v-data-table
                                                    v-bind:headers="headersConfirmedLessons"
                                                    :items="itemsConfirmedLessons"
                                                    hide-actions>
                                                <template slot="items" slot-scope="props">
                                                    <td>{{ props.item.confirmedDateTime }}</td>
                                                    <td class="text-xs-left">{{ props.item.lessonName }}</td>
                                                    <td class="text-xs-left">{{ currency(props.item.price) }}</td>
                                                    <td class="text-xs-left">{{ props.item.lessonCategory }}</td>
                                                    <td class="text-xs-left">{{ props.item.verificationCode }}</td>
                                                    <td>
                                                        <h4 class="indigo--text text-xs-center" v-if="!props.item.adminVerified">等待驗證中</h4>
                                                        <h4 class="green--text text-xs-center" v-if="props.item.adminVerified">已確認</h4>
                                                    </td>
                                                </template>
                                                </v-data-table>
                                            </v-flex>
                                        </v-layout>
                                    </v-card-text>
                                </v-card>
                            </v-flex>
                        </v-layout>
                    </v-card-text>
                </v-card>
            </v-flex>
            <!-- events -->
            <v-flex xs12 sm12>
                <v-card class="mb-30">
                    <v-card-title primary-title class="teal white--text">
                        <h3>活動</h3>
                    </v-card-title>
                    <v-card-text>
                        <v-layout row wrap>
                            <v-flex xs12 sm12>
                                <v-card class="mb-30">
                                    <v-card-title class="green white--text">
                                        加入的活動(向右滑動了解詳情)
                                    </v-card-title>
                                    <v-card-text>
                                        <v-layout row wrap>
                                            <v-flex xs12 sm12>
                                                 <v-data-table
                                                    v-bind:headers="headersAppliedEvents"
                                                    :items="itemsAppliedEvents"
                                                    hide-actions>
                                                <template slot="items" slot-scope="props">
                                                    <td>{{ props.item.applyDateTime }}</td>
                                                    <td class="text-xs-left">{{ props.item.eventName }}</td>
                                                    <td class="text-xs-left">{{ currency(props.item.fee) }}</td>
                                                    <td class="text-xs-left">{{ props.item.location }}</td>
                                                    <td class="text-xs-left">{{ props.item.date }}</td>
                                                    <td class="text-xs-left">{{ props.item.time }}</td>
                                                    <td class="text-xs-left">{{ props.item.verificationCode }}</td>
                                                    <td>
                                                        <v-btn class="info" block @click="showEventTransferInfo(props.item)">
                                                            匯款資訊
                                                        </v-btn>
                                                    </td>
                                                    <td>
                                                        <v-btn class="primary" block v-if="!props.item.userStatus" @click="confirmedAppliedEvent(props.item.docId)">
                                                            已確認匯款
                                                        </v-btn>
                                                        <h4 class="indigo--text text-xs-center" v-if="props.item.userStatus === 'Confirmed'">等待驗證中</h4>
                                                    </td>
                                                    <td>
                                                        <v-btn class="btn-danger" block v-if="!props.item.userStatus" @click="deleteAppliedEvent(props.item.docId)">
                                                            刪除
                                                        </v-btn>
                                                    </td>
                                                </template>
                                                </v-data-table>
                                            </v-flex>
                                        </v-layout>
                                    </v-card-text>
                                </v-card>
                            </v-flex>
                            <!-- events confirmed to attend -->
                            <v-flex xs12 sm12>
                                <v-card class="mb-30">
                                    <v-card-title class="green white--text">
                                        確認的活動(向右滑動了解詳情)
                                    </v-card-title>
                                    <v-card-text>
                                        <v-layout row wrap>
                                            <v-flex xs12 sm12>
                                                <v-data-table
                                                    v-bind:headers="headersConfirmedEvents"
                                                    :items="itemsConfirmedEvents"
                                                    hide-actions>
                                                <template slot="items" slot-scope="props">
                                                    <td>{{ props.item.applyDateTime }}</td>
                                                    <td class="text-xs-left">{{ props.item.eventName }}</td>
                                                    <td class="text-xs-left">{{ currency(props.item.fee) }}</td>
                                                    <td class="text-xs-left">{{ props.item.location }}</td>
                                                    <td class="text-xs-left">{{ props.item.date }}</td>
                                                    <td class="text-xs-left">{{ props.item.time }}</td>
                                                    <td class="text-xs-left">{{ props.item.verificationCode }}</td>
                                                    <td>
                                                        <h4 class="indigo--text text-xs-center" v-if="!props.item.adminVerified">等待驗證中</h4>
                                                        <h4 class="green--text text-xs-center" v-if="props.item.adminVerified">已確認</h4>
                                                    </td>
                                                </template>
                                                </v-data-table>
                                            </v-flex>
                                        </v-layout>
                                    </v-card-text>
                                </v-card>
                            </v-flex>
                        </v-layout>
                    </v-card-text>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
/* eslint-disable */
import { mapGetters } from 'vuex'
import firebase from 'firebase'
import swal from 'sweetalert2'
import formatCurrency from 'format-currency'

export default {
    data() {
        return {
            systemLoadingDialog: false,
            user: "",
            search: '',
            // applied lessons table
            headersAddedLessons: [
                {
                    text: '加入時間',
                    align: 'left',
                    sortable: true,
                    value: 'applyDateTime'
                },
                { text: '課程名稱', value: 'name' },
                { text: '費用', value: 'price' },
                { text: '課程分類', value: 'category' },
                { text: '匯款備註代碼', value: 'verificationCode' },
                { text: '', value: 'confirmed' },
                { text: '', value: 'delete' }
            ],
            itemsAddedLessons: [],
            // payment confirmed table
            headersConfirmedLessons: [
                {
                    text: '加入時間',
                    align: 'left',
                    sortable: true,
                    value: 'applyDateTime'
                },
                { text: '課程名稱', value: 'name' },
                { text: '費用', value: 'price' },
                { text: '課程分類', value: 'category' },
                { text: '匯款備註代碼', value: 'verificationCode' },
                { text: '', value: 'confirmed' },
                { text: '', value: 'delete' }
            ],
            itemsConfirmedLessons: [],
            // applied events table
            headersAppliedEvents: [
                {
                    text: '加入時間',
                    align: 'left',
                    sortable: true,
                    value: 'applyDateTime'
                },
                { text: '活動名稱', value: 'name' },
                { text: '費用', value: 'fee' },
                { text: '地點', value: 'location' },
                { text: '日期', value: 'date' },
                { text: '時間', value: 'time' },
                { text: '匯款備註代碼', value: 'verificationCode' },
                { text: '', value: 'confirmed' },
                { text: '', value: 'delete' }
            ],
            itemsAppliedEvents: [],
            // applied events table
            headersConfirmedEvents: [
                {
                    text: '加入時間',
                    align: 'left',
                    sortable: true,
                    value: 'applyDateTime'
                },
                { text: '活動名稱', value: 'name' },
                { text: '費用', value: 'fee' },
                { text: '地點', value: 'location' },
                { text: '日期', value: 'date' },
                { text: '時間', value: 'time' },
                { text: '匯款備註代碼', value: 'verificationCode' },
                { text: '', value: 'confirmed' },
                { text: '', value: 'delete' }
            ],
            itemsConfirmedEvents: []
        }
    },
    created() {
        this.getMyAppliedLessons()
        this.getMyConfirmedLessons()
        this.getMyAppliedEvents()
        this.getMyConfirmedEvents()
    },
    methods: {
        // get data section
        async getMyAppliedLessons () {
            try {
                this.systemLoadingDialog = true
                var db = firebase.firestore()
                var query = await db.collection('myLessons')
                    .where('userUid', '==', firebase.auth().currentUser.uid)
                    .where('adminVerified', '==', false)
                    .get()
                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
                this.itemsAddedLessons = data
                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        async getMyConfirmedLessons () {
            try {
                this.systemLoadingDialog = true
                var db = firebase.firestore()
                var query = await db.collection('myLessons')
                    .where('userUid', '==', firebase.auth().currentUser.uid)
                    .where('userStatus', '==', 'Confirmed')
                    .get()
                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
                this.itemsConfirmedLessons = data
                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        async getMyAppliedEvents () {
            try {
                this.systemLoadingDialog = true
                var db = firebase.firestore()
                var query = await db.collection('myEvents')
                    .where('userUid', '==', firebase.auth().currentUser.uid)
                    .where('adminVerified', '==', false)
                    .get()
                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
                this.itemsAppliedEvents = data
                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        async getMyConfirmedEvents () {
            try {
                this.systemLoadingDialog = true
                var db = firebase.firestore()
                var query = await db.collection('myEvents')
                    .where('userUid', '==', firebase.auth().currentUser.uid)
                    .where('userStatus', '==', 'Confirmed')
                    .get()
                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
                this.itemsConfirmedEvents = data
                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        // showing transfer info
        showLessonTransferInfo (doc) {
            // console.log(doc)
            let transferInfo = `<ul style="text-align: left; padding: 20px;">
                <li>銀行名稱： ${ doc.bankName}</li>
                <li>銀行代號： ${ doc.bankCode}</li>
                <li>戶名： ${ doc.accountName}</li>
                <li>匯款帳號： ${ doc.bankAccount}</li>
                <li>往來行： ${ doc.correspondent}</li>
                <hr>
                <li>活動名稱： ${ doc.lessonName}</li>
                <li>金額： ${ doc.price || 0}</li>
                <li>確認碼： ${ doc.verificationCode}</li>
            </ul>
            <small>匯款時請備註確認碼</small>
            <small>如果此購買為0元，至用戶中心點擊確認即可</small>`
            swal(
                '匯款資訊',
                transferInfo,
                'info'
            )
        },
        showEventTransferInfo (doc) {
            // console.log(doc)
            let transferInfo = `<ul style="text-align: left; padding: 20px;">
                <li>銀行名稱： ${ doc.bankName}</li>
                <li>銀行代號： ${ doc.bankCode}</li>
                <li>戶名： ${ doc.accountName}</li>
                <li>匯款帳號： ${ doc.bankAccount}</li>
                <li>往來行： ${ doc.correspondent}</li>
                <hr>
                <li>活動名稱： ${ doc.eventName}</li>
                <li>金額： ${ doc.fee || 0}</li>
                <li>確認碼： ${ doc.verificationCode}</li>
            </ul>
            <small>匯款時請備註確認碼</small>
            <small>如果此購買為0元，至用戶中心點擊確認即可</small>`
            swal(
                '匯款資訊',
                transferInfo,
                'info'
            )
        },
        // update section
        async confirmedAppliedLesson (docId) {
            try {
                this.systemLoadingDialog = true
                var db = firebase.firestore()
                await db.collection('myLessons').doc(docId).update({
                    userStatus: 'Confirmed',
                    confirmedDateTime: this.parseDateTimeFormat()
                })
                swal(
                    '管理員驗證中',
                    '',
                    'success'
                )
                this.getMyAppliedLessons()
                this.getMyConfirmedLessons()
                this.systemLoadingDialog = false
            } catch (error) {
                console.error(error)
            }
        },
        async deleteAppliedLesson (docId) {
            try {
                var result = await swal({
                    title: '確定刪除',
                    text: "此動作將無法收回",
                    type: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: '確認刪除',
                    cancelButtonText: '取消'
                })
                if (result.value) {
                    this.systemLoadingDialog = true
                    var db = firebase.firestore()
                    await db.collection('myLessons').doc(docId).delete()
                    swal(
                        '刪除成功',
                        '',
                        'success'
                    )    
                }
                this.getMyAppliedLessons()
                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        async confirmedAppliedEvent (docId) {
            try {
                this.systemLoadingDialog = true
                var db = firebase.firestore()
                await db.collection('myEvents').doc(docId).update({
                    userStatus: 'Confirmed',
                    confirmedDateTime: this.parseDateTimeFormat()
                })
                swal(
                    '管理員驗證中',
                    '',
                    'success'
                )
                this.getMyAppliedEvents()
                this.getMyConfirmedEvents()
                this.systemLoadingDialog = false
            } catch (error) {
                console.error(error)
            }
        },
        async deleteAppliedEvent (docId) {
            try {
                var result = await swal({
                    title: '確定刪除',
                    text: "此動作將無法收回",
                    type: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: '確認刪除',
                    cancelButtonText: '取消'
                })
                if (result.value) {
                    this.systemLoadingDialog = true
                    var db = firebase.firestore()
                    await db.collection('myEvents').doc(docId).delete()
                    swal(
                        '刪除成功',
                        '',
                        'success'
                    )
                    this.getMyAppliedEvents()
                }
                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        // utilities
        currency (money) {
            let opts = { format: '%s%v', symbol: '$' }
            return formatCurrency(money, opts)
        },
        parseDateTimeFormat (type) {
            var now = new Date()
            var mm = now.getMonth() + 1 // getMonth() is zero-based
            var dd = now.getDate()
            var hour = now.getHours()
            var min = now.getMinutes()
            var sec = now.getSeconds()
            if (type === 'date') {
                var string = now.getFullYear() + '-' + (mm > 9 ? '' : '0') + mm + '-' + (dd > 9 ? '' : '0') + dd
                return string
            } else {
                var string = now.getFullYear() + '/' + (mm > 9 ? '' : '0') + mm + '/' + (dd > 9 ? '' : '0') + dd + ' ' + (hour > 9 ? '' : '0') + hour + ':' + (min > 9 ? '' : '0') + min + ':' + (sec > 9 ? '' : '0') + sec
                return string
            }
        }
    },    
    watch: {
        //
    },
    computed: {
        ...mapGetters({
            getLang: 'getLang'
        })
    }
};
</script>



