<template>
  <div class="session-banner">
    <v-container px-0>
      <v-layout row wrap align-center>
          <v-flex xs12 class="text-xs-center">
              <v-dialog v-model="systemLoadingDialog" max-width="500px" class="mx-auto">
                  <v-card class="text-xs-center">
                      <v-card-text>
                          <h3>{{ $lang.strings.system_running }}......</h3>
                          <br>
                          <v-progress-circular indeterminate :size="70" :width="7" color="blue"></v-progress-circular>
                      </v-card-text>
                  </v-card>
              </v-dialog>
          </v-flex>
      </v-layout>
      <v-layout row wrap>
        <v-flex xs12 sm10 md6 lg5 mx-auto>
          <div class="mb-70">
            <!-- <router-link to="/" class="d-block text-xs-center"><img src="/static/img/session-logo.png" /></router-link> -->
          </div>
          <div class="session-block">
            <div class="session-head text-xs-center">
              <div class="div-icon mb-3">
                <i class="ti-lock font-2x"></i>
              </div>
              <h3 class="mb-4">登入</h3>
              <p class="fs-14 px-5">請輸入帳號密碼</p>
            </div>
            <v-form v-model="valid" class="mb-4">
              <v-text-field @keyup.enter="signIn" label="E-mail信箱" v-model="email" :rules="emailRules" required></v-text-field>
              <v-text-field @keyup.enter="signIn" label="密碼" v-model="password" type="password" :rules="passwordRules" required></v-text-field>
              <v-checkbox label="記住我" v-model="checkbox" :rules="[v => !!v || 'You must agree to continue!']" required></v-checkbox>
              <v-btn @click="signIn" block class="btn-gradient-primary">登入</v-btn>
              <v-btn to="/session/sign-up" block class="btn-gradient-warning">註冊</v-btn>
            </v-form>
            <!-- <p class="text-xs-center mb-0 log-link"><router-link to="/session/login">忘記密碼請洽詢管理者</router-link></p> -->
          </div>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import firebase from 'firebase'
import swal from 'sweetalert2'

export default {
  data() {
    return {
      checkbox: false,
      valid: false,
      email: "",
      emailRules: [
        v => !!v || "E-mail是需要的",
        v =>
          /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) ||
          "E-mail格式不正確"
      ],
      password: "",
      passwordRules: [v => !!v || "密碼是需要的"],
      systemLoadingDialog: false
    };
  },
  methods: {
    async signIn() {
      try {
        var db = firebase.firestore()
        this.systemLoadingDialog = true

        // set State
        await firebase.auth().setPersistence(firebase.auth.Auth.Persistence.SESSION)

        // since firebase will handle the user registrations, no need to pass to server
        await firebase.auth().signInWithEmailAndPassword(this.email, this.password)

        var user = firebase.auth().currentUser
        var query = await db.collection('users').where('userUid', '==', firebase.auth().currentUser.uid).get()
        var data = query.docs[0].data()
        data.emailVerified = user.emailVerified
        data.docId = query.docs[0].id
        // console.log(data)
        this.$store.dispatch('setUser', data)

        this.systemLoadingDialog = false
        swal(
          'Good!',
          '登入成功',
          'success'
        )
        this.$router.push({ path: '/' })
      } catch (error) {
        this.systemLoadingDialog = false
        console.log(error)
        var errorCode = error.code
        var errorMessage = error.message
        switch(errorCode) {
          case 'auth/invalid-email':
            swal(
              'Oops!',
              '錯誤的email',
              'error'
            )
            break;
          case 'auth/wrong-password':
            swal(
              'Oops!',
              '錯誤的密碼!!忘記密碼請洽詢管理者',
              'error'
            )
            break;
          case 'auth/user-not-found':
            swal(
              'Oops!',
              '沒有找到使用者',
              'error'
            )
            break;
          default:
            swal(
              'Oops!',
              errorMessage,
              'error'
            )
        }
      }
    }
  }
};
</script>



