<template>
    <v-container fluid grid-list-xl py-0>
        <v-layout row wrap align-center>
            <v-flex xs12 class="text-xs-center">
                <v-dialog v-model="systemLoadingDialog" max-width="500px" class="mx-auto">
                    <v-card class="text-xs-center">
                        <v-card-text>
                        <h3>{{ $lang.strings.system_running}}......</h3>
                        <br>
                        <v-progress-circular indeterminate :size="70" :width="7" color="blue"></v-progress-circular>
                        </v-card-text>
                    </v-card>
                </v-dialog>
            </v-flex>
        </v-layout>
        <v-layout row wrap>
            <v-flex xs12 sm12>
              <!-- <v-card class="mb-30" v-if="manageLessonStatus"> -->
              <v-card class="mb-30">
                    <v-card-title primary-title class="indigo white--text">
                        <!-- <h3>{{ mode }}</h3> -->
                        <h3>{{$lang.strings.myOnlineLessons}}</h3> 
                    </v-card-title>
                    <v-card-text>
                        <v-layout row wrap>
                            <v-flex xs12 sm6 offset-sm3>
                                <!-- this select binded on watch methods -->
                                <v-select
                                    :items="verifiedLessonsList"
                                    label="可選擇的課程"
                                    item-text="lessonName"
                                    item-value="lessonId"
                                    v-model="selectItemsVerifiedLessonID"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12 sm6 offset-sm3>
                                <!-- this select binded on watch methods -->
                                <v-select
                                    :items="verifiedLessonsVideosList"
                                    label="可選擇的影片列表"
                                    item-text="lessonVideoName"
                                    item-value="lessonVideo"
                                    v-model="videoSrc"
                                ></v-select>
                            </v-flex>
                            <v-flex xs12 sm12>
                              <vue-plyr>
                                <video :src="videoSrc">
                                </video>
                              </vue-plyr>
                            </v-flex>
                            <v-flex xs12 sm12>
                                <v-card-title primary-title class="indigo white--text">
                                    <!-- <h3>{{ mode }}</h3> -->
                                    <h3>課程文檔</h3> 
                                </v-card-title>
                                <v-data-table
                                    v-bind:headers="headersVerifiedLessons"
                                    :items="verifiedLessonItems"
                                    hide-actions>
                                  <template slot="items" slot-scope="props">
                                      <td>{{ props.item.lessonFileUpdateTime }}</td>
                                      <td class="text-xs-left">{{props.item.lessonFileName}}</td>
                                      <td class="text-xs-left">
                                        <a :href="props.item.lessonFile" :download="props.item.lessonFileName"
                                        target="_blank">檔案下載</a>
                                      </td>
                                      <!-- <td>
                                          <h4 class="indigo--text text-xs-center" v-if="!props.item.adminVerified">等待驗證中</h4>
                                          <h4 class="green--text text-xs-center" v-if="props.item.adminVerified">已確認</h4>
                                      </td> -->
                                  </template>
                                </v-data-table>
                            </v-flex>
                        </v-layout>
                    </v-card-text>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
/* eslint-disable */
import { mapGetters } from 'vuex'
import firebase from 'firebase'
import swal from 'sweetalert2'

export default {
    data() {
        return {
            systemLoadingDialog: false,
            user: "",
            verifiedLessonsList: [],
            selectItemsVerifiedLessonID: '',
            //videos
            verifiedLessonsVideosList:[],
            //now showing video
            videoSrc:"",
            //files
            verifiedLessonItems: [],

            // applied lessons table
            headersVerifiedLessons: [
                {
                    text: '更新時間',
                    align: 'left',
                    sortable: true,
                    value: 'updateTime'
                },
                { text: '檔案名稱', value: 'price' },
                { text: '檔案下載', value: 'download' }
            ],
           
        }
    },
    created() {
        this.getVerifiedLessons();
    },
    methods: {
        async getVerifiedLessons () {
            try {
                this.systemLoadingDialog = true
                var db = firebase.firestore()
                var query = await db.collection('myLessons')
                    .where('userUid', '==', firebase.auth().currentUser.uid)
                    .where('adminVerified', '==', true)
                    .get()
                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
                
                this.verifiedLessonsList = data
                this.selectItemsVerifiedLessonID = data[0].lessonId
                console.log(this.verifiedLessonsList);

                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        async getVideoListAndLessonListFrom(lessonID){
             try {
                this.systemLoadingDialog = true
                var db = firebase.firestore()
               
                var query = await db.collection("onlineLessons")
                    .where("lessonId", "==",lessonID)
                    .get()
                var data = []
                query.docs.forEach(doc => {
                    var item = doc.data()
                    item.docId = doc.id
                    data.push(item)
                })
                // this.selectItemsVerifiedLessonID = data[0].lessonId

                this.verifiedLessonsVideosList = data.filter(ele=>{
                    return ele.lessonVideoName
                })
                
                if(this.verifiedLessonsVideosList.length !== 0){
                    this.videoSrc = this.verifiedLessonsVideosList[0].lessonVideo
                }

                this.verifiedLessonItems = data.filter(ele=>{
                    return ele.lessonFileName
                }).map(ele=>{
                    ele.lessonFileUpdateTime = new Date(ele.lessonFileUpdateTime).toLocaleDateString()
                    return ele
                })
                
                console.log(this.verifiedLessonsVideosList);
                console.log(this.videoSrc);
                console.log(this.verifiedLessonItems);

                this.systemLoadingDialog = false
            } catch (error) {
                this.systemLoadingDialog = false
                console.error(error)
            }
        },
        // async setVideoAndFilesWith(lessonID){
        //     try {
        //         this.systemLoadingDialog = true
        //         var db = firebase.firestore()
        //         var query = await db.collection('lessons').doc(lessonID).get()
        //         if(query.exists){
        //           var data = query.data()
        //           //following should be array,now is string
        //           this.videoSrc = data.lessonVideo

        //           this.verifiedLessonItems = [data]
        //           console.log(this.verifiedLessonItems)
        //         }
        //         this.systemLoadingDialog = false
        //     } catch (error) {
        //         this.systemLoadingDialog = false
        //         console.error(error)
        //     }
        // }
    },    
    watch: {
        selectItemsVerifiedLessonID: function () {
          this.getVideoListAndLessonListFrom(this.selectItemsVerifiedLessonID)
          console.log(this.selectItemsVerifiedLessonID)
            // this.getLessonsOfCat(this.selectCategory)
        },
        
    },
    computed: {
        ...mapGetters({
            getLang: 'getLang'
        })
    }
};
</script>



