Vue.use(Router)

let router = new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      component: Full,
      redirect: '/users/user-profile',
      children: [
        {
          path: '/dashboard/dashboard-v1',
          component: DashboardOne,
          meta: {
            title: 'Dashboard V1',
            breadcrumb: 'Dashboard / Dashboard V1'
          }
        },
        {
          path: '/dashboard/dashboard-v2',
          component: DashboardTwo,
          meta: {
            title: 'Dashboard V2',
            breadcrumb: 'Dashboard / Dashboard V2'
          }
        },
        // admin
        {
          path: '/admin/cms',
          component: CMS,
          meta: {
            title: 'Content Management',
            breadcrumb: 'Admin / Content Management'
          }
        },
        {
          path: '/admin/cms-about',
          component: CMSAbout,
          meta: {
            title: 'Content Management - About Us',
            breadcrumb: 'Admin / Content Management / About Us'
          }
        },
        {
          path: '/admin/manage-events',
          component: ManageEvents,
          meta: {
            title: 'Events',
            breadcrumb: 'Admin / Events'
          }
        },
        {
          path: '/admin/manage-all-events',
          component: ManageAllEvents,
          meta: {
            title: 'AllEvents',
            breadcrumb: 'Admin / Events'
          }
        },
        // admin academy
        {
          path: '/admin/academy-categories',
          component: AcademyCategory,
          meta: {
            title: 'Manage Academy Categories',
            breadcrumb: 'Admin / Manage Academy Categories'
          }
        },
        {
          path: '/admin/academy',
          component: Academy,
          meta: {
            title: 'Manage Academy',
            breadcrumb: 'Admin / Manage Academy'
          }
        },
        // admin verify user lessons and events
        {
          path: '/admin/verify-users-lessons',
          component: VerifyUserLessons,
          meta: {
            title: 'Verify Users Lessons',
            breadcrumb: 'Admin / Verify Users Lessons'
          }
        },
        {
          path: '/admin/verify-users-events',
          component: VerifyUserEvents,
          meta: {
            title: 'Verify Users Events',
            breadcrumb: 'Admin / Verify Users Events'
          }
        },
        {
          path: '/admin/manage-users-lessons',
          component: ManageUserLessons,
          meta: {
            title: 'Manage Users Lessons',
            breadcrumb: 'Admin / Manage Users Lessons'
          }
        },
        {
          path: '/admin/users-info',
          component: UsersInfo,
          meta: {
            title: 'Users Information',
            breadcrumb: 'Admin / Users Information'
          }
        },
        // // users
        {
          path: '/users/user-profile',
          component: UserProfile,
          meta: {
            title: 'User Profile',
            breadcrumb: 'Users / User Profile'
          }
        },
        {
          path: '/users/user-profile-update',
          component: UserProfileUpdate,
          meta: {
            title: 'User Profile Update',
            breadcrumb: 'Users / User Profile Update',
            userUpdatePage: true
          }
        },
        {
          path: '/users/my-lessons-and-events',
          component: MyLessonsAndEvents,
          meta: {
            title: 'My Lessons and Events',
            breadcrumb: 'Users / My Lessons and Events'
          }
        },
        {
          path: '/users/my-online-lessons',
          component: MyOnlineLessons,
          meta: {
            title: 'My Online Lessons',
            breadcrumb: 'Users / My Online Lessons'
          }
        }
        //
      ]
    },
    {
      path: '/session/sign-up',
      component: SignUp,
      meta: {
        title: 'Sign Up',
        breadcrumb: 'Session / Sign Up',
        entrance: true
      }
    },
    {
      path: '/session/login',
      component: Login,
      meta: {
        title: 'Login',
        breadcrumb: 'Session / Login',
        entrance: true
      }
    }
    // {
    //   path: '/session/lock-screen',
    //   component: LockScreen,
    //   meta: {
    //     title: 'Lock Screen',
    //     breadcrumb: 'Session / Lock Screen'
    //   }
    // }
  ]
})

router.beforeEach((to, from, next) => {
  let currentUser = firebase.auth().currentUser
  // let requiresAuth = to.matched.some(record => record.meta.requiresAuth)
  let entrance = to.matched.some(record => record.meta.entrance)
  if (!entrance && !currentUser) {
    next('/session/login')
  } else if (entrance && currentUser) {
    next('/')
  } else {
    next()
  }
})

export default router



